
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
Alert,
},
data() {
return {
departmentid:this.$store.getters.user.departmentid,
active:[],
show:false,
msg:'',
userId:cookie.get('userId'),
isActive:false
};
},
methods:{
getActivityMore(){
axios({
method: 'get',
url: '/dangjian/active/getParticipateActive',
params: {
pageNum:1,
pageSize:3,
departmentid:this.departmentid,
userId:this.$store.getters.user.userid
}
}) .then((res)=> {
console.log(res.data.list)
this.active=res.data.list;
})
.catch(function (error) {
console.log(error);
});


},
submit(id){
console.log(id)

axios({
method: 'post',
url: '/dangjian/active/participate',
params: {
userId:this.userId,
activeId:id,
departmentid:this.departmentid,


}
}) .then((res)=> {


this.msg=res.msg,
this.show=true



})
.catch(function (error) {
console.log(error);
});

},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
}
} ,
mounted() {
this.getActivityMore();
console.log(this.$store.getters.user.userid);

}
};



